<template>
  <div>
    <!-- <div class="setting" @click="openSetting">
      <van-icon color="gray" size="20" name="setting-o" />
    </div> -->
    <div class="content">
      <p class="logo">
        <!-- <img src="@assets/images/crm.png" /> -->
      </p>

      <van-nav-bar
        title="个人资料修改"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
      />

      <van-cell-group>
        <van-field disabled v-model="mobile" label="登录账号" placeholder="" />
        <van-field
          v-model="user_login"
          label="用户名"
          type="text"
          placeholder="请输入用户名"
        />
        <van-field
          v-model="user_nickname"
          label="用户昵称"
          placeholder="请输入用户昵称"
          type="text"
        />
        <van-field
          v-model="signature"
          type="text"
          label="公司名称"
          placeholder="请输入公司名称"
        />
      </van-cell-group>

      <div class="submit" @click="submitLogin">确认修改</div>
    </div>
  </div>
</template> 
<script>
import { upUser, getUserInfo } from "@api/user";
import { Icon, Toast, NavBar, Field } from "vant";
import localStorage from "@utils/store/localStorage";

import { trim } from "@utils";

export default {
  name: "Changeinfo",
  components: {
    [Icon.name]: Icon,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
    [Field.name]: Field,
  },
  data: function () {
    return {
      checked: true,
      user_login: "",
      signature: "",
      user_nickname: "",

      mobile: "",
      show: false,
    };
  },
  mounted: function () {
    getUserInfo()
      .then(({ data }) => {
        this.user_nickname = data.user.user_nickname;
        this.signature = data.user.signature;
        this.user_login = data.user.user_login;
        this.mobile = data.user.mobile;
      })
      .catch((e) => {
        Toast(e.msg);
      });
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    openSetting() {
      this.show = true;
    },

    submitLogin() {
      if (trim(this.user_login) == "") {
        Toast("请输入用户名");
      } else {
        upUser({
          user_nickname: this.user_nickname,
          user_login: this.user_login,
          signature: this.signature,
        })
          .then(({ data }) => {
            Toast("修改成功");
            location.reload();
          })
          .catch((e) => {
            Toast(e.msg);
          });
      }
    },
  },
};
</script>
<style scoped>
p {
  margin-bottom: 15px;
  text-align: center;
}

input {
  height: 50px;
  font-size: 18px;
  line-height: 50px;
  border: 0;
  border: 1px solid #ccc;
  padding-left: 10px;
  width: 82%;
  outline: none;
}

.submit {
  background-color: #40b9ec;
  color: #fff;
  height: 40px;
  line-height: 40px;
  text-align: center;
  width: 82%;
  margin: 50px auto 10px;
}

.logo {
  text-align: center;
  margin-bottom: 0;
  margin-top: 10px;
}

.logo img {
  width: 150px;
}

.bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  font-size: 12px;
  line-height: 10px;
  color: #999;
}

.forgetPassword {
  text-align: center;
  font-size: 12px;
  color: #999;
  padding-top: 10px;
}
.setting {
  position: fixed;
  top: 10px;
  left: 10px;
  color: red;
}
.content .crm {
  font-size: 16px;
  color: #40b9ec;
  text-align: center;
  line-height: 50px;
  font-weight: bold;
}
.check {
  margin-left: 35px;
  font-size: 12px;
  color: #999;
  margin-top: -10px;
}
.check input {
  margin-right: 5px;
  width: 15px;
}
.forgetPassword {
  display: block;
  text-align: center;
}
</style>
